@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* article style */
.article-entry {
	position: relative;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; }

	&::before {
		@include pseudo();
		@include position(1em, 0);
		@include size(100px, 0);
		border-top: 1px solid $tape-slider-color;

		@include media('<desk-md') {
			content: none; } }

	p, ul, li {
		&:last-child {
			margin-bottom: 0; } } }

.article-entry__holder {
	position: relative;
	overflow: hidden;
	max-height: ($font-size*$line-height*3);
	margin-bottom: 2rem; }

.text-slide__shadow {
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	height: 40px;
	@include linear-gradient(to top, white 0, rgba(255, 255, 255, 0) 100%);
	pointer-events: none; }

.text-slide__button {
	line-height: 1;

	a {
		display: inline-block;
		vertical-align: top;
		position: relative;
		@include font(1.2rem, round-float(14/12, 1));
		color: $text-color;
		text-decoration: none;
		text-transform: uppercase;
		padding-right: 17px;
		user-select: none;

		span {
			border-bottom: 1px dotted; }

		.no-touchevents &:hover {
			color: $link-color; }

		&::before {
			@include pseudo();
			right: 0;
			top: 0.4rem;
			@include triangle(bottom, $text-color, 3px, 2); } }

	.btn-arrow--bottom.active {
		&::before {
			transform: rotate(-0.5turn); } } }

.history-info__text {

	p:last-child {
		margin-bottom: 0; } }

.text-slide__button {
	.btn-arrow--bottom.active > span {
		&::before {
			transform: rotate(-45deg); }

		&::after {
			transform: rotate(45deg); } } }

.article-footer {
	position: relative;
	height: $article-footer-height;
	background-color: $bg-color;
	border: 1px solid $bg-color-alt;
	@include margin-horizontal(auto);
	@include padding-horizontal(35px);
	z-index: 1;
	@include clear-spacing;

	@include media('<desk-lg') {
		@include padding-horizontal(20px); }

	&::before {
		@include pseudo-inline-block-relative();
		vertical-align: middle;
		height: 100%; }

	.text-sm {
		display: block;
		@include font(10px, round-float(12/10, 2));
		color: #4c4c4c;
		letter-spacing: 0.2em;
		text-transform: uppercase;
		margin-bottom: 5px;

		&:last-child {
			margin-bottom: 0; } }

	.text-article-title {
		display: block;
		@include font-alt-bold;
		@include font(24px, round-float(28/24, 2));
		@include truncate(100%); }

	[class*="arr-"] {
		position: absolute;
		top: 50%;
		@include size(12px, 10px);
		transform: translate(0, -50%); } }

.article-footer__cell {
	position: relative;
	@include display-inline-block-middle;
	@include padding-horizontal(50px);
	width: round-float(100%/2 - 0.02, 2);
	@include font($font-size, $line-height);
	letter-spacing: $letter-spacing;
	text-decoration: none;
	color: $text-color;

	@include media('<desk-lg') {
		@include padding-horizontal(30px); }

	&::before {
		@include pseudo();
		@include position(50%, 0);
		@include size(0, 50px);
		border-left: 1px solid $bg-color-alt;
		transform: translate(0, -50%); }

	&:first-child {
		&::before {
			content: none; } }

	&:hover {
		color: $base-color;

		.arr-left {
			& > i {
				border-right-color: $base-color; } }

		.arr-right {
			& > i {
				border-left-color: $base-color; } } } }

.article-footer__cell--left {
	text-align: left;

	[class*="arr-"] {
		left: 0; } }

.article-footer__cell--right {
	text-align: right;

	[class*="arr-"] {
		right: 0; } }

/* article style end */
