@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* NAVIGATION */
/* nav list */
// общие стили для пунктов навигации
.nav {
	position: relative; }

.nav-list {
	list-style: none;
	margin: 0 0 58px;
	padding: 0;

	@include media('<desk-hd') {
		margin: 0 0 48px; }

	@include media('<desk-lg') {
		margin: 0 0 32px; }

	&:last-child {
		margin-bottom: 0; }

	li {
		position: relative;
		overflow: hidden; }

	.nav__tab {
		position: relative; }

	a {
		display: block;
		position: relative;
		padding: 6px 0;
		color: $text-color-base;
		border-color: currentColor; // для нижнего подчеркивания
		text-decoration: none;
		@include font-alt-medium;
		@include font(1.8rem, round-float(24/18, 2));
		user-select: none;
		-webkit-tap-highlight-color: transparent;

		// hover
		.no-touchevents &:hover {
			color: $base-color; } } }

// общие стили для подменю навигации
.nav__drop {
	position: relative;
	display: none;

	.no-js & {
		display: block; } }

// стили для пунктов навигации, у которых ЕСТЬ ПОДМЕНЮ
.nav-list li.has-drop > .nav__tab > a {
	padding-right: (round($nav-arrow-size+10)); }

// стили для пункта навигации ТЕКУЩЕЙ страницы
.no-touchevents {
	.nav-list li.current {
		& > a, & > .nav__tab a {
			&, &:hover {
				color: $base-color; } } } }

// особые стили для пунктов навигации ПЕРВОГО урованя
.nav-list > li {
	& > a, & > .nav__tab a {
		@include padding-vertical(4px);
		@include font(3.0rem, round-float(34/30, 2)); } }

// СТРЕЛКИ навигации
.nav__angle {
	position: absolute;
	right: 0;
	top: 0;
	width: $nav-arrow-size;
	height: 100%;
	//transition:
	//roperty: transform, color, background-color
	//uration: $animation-speed
	z-index: 2;
	cursor: pointer;
	display: none;

	// убрать подсветку при клике на тачскринах
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	li.has-drop > .nav__tab > & {
		display: block; }

	i {
		position: absolute;
		@include position(50%, 50%);
		@include size(7px);
		margin: -4px 0 0 -4px;
		@include font(0, 0);
		pointer-events: none;
		will-change: transform;
		transition: {
			property: transform;
			duration: $animation-speed; }

		&::before, &::after {
			@include pseudo();
			@include position(50%, 0);
			height: 2px;
			width: 7px;
			backface-visibility: hidden;
			background-color: currentColor;
			//$prop: background-color, transform
			//will-change: $prop
			//transition:
			//roperty: $prop
 }			//uration: $animation-speed

		&::before {
			transform: rotate(45deg);
			margin: 0 0 0 -2px; }

		&::after {
			transform: rotate(-45deg);
			margin: 0 0 0 2px; } } }

// стили стрелок навигации для разных состояний
.nav__angle {

	// активное (подменю открыто)
	li.is-open > .nav__tab & i {
		transform: rotate(0.5turn); }

	// ховер
	.no-touchevents li > .nav__tab &:hover i {
		&::before, &::after {
			background-color: $base-color; } } }

// особые стили для пунктов ПОДМЕНЮ
.nav__drop {
	// особые стили для пунктов навигации ВТОРОГО урованя
	// и ниже
	ul {
		@include padding-vertical(5px); }

	a {
		position: relative;
		padding-left: round(nth($nav-space, 2));

		&::before {
			content: "–";
			display: block;
			position: absolute;
			left: 0;
			top: 0.3em; } }

	.nav__drop {
		// особые стили для пунктов навигации ТРЕТЬЕГО урованя
		// и ниже
		li {
			padding-left: round(nth($nav-space, 2));

			//a
			//ont-size: 1.4rem
 } } }			//include font-normal

/* nav list end */
/* NAVIGATION end */


