@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

.m-wrapper {
	position: relative;
	padding-right: 1px;
	margin-bottom: $bottom-space--lg;
	z-index: 15;
	//user-select: none

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

/* 404 page */
.err {
	display: table;
	width: 100%; }

.err__holder {
	display: table-cell;
	vertical-align: middle;
	@include padding-vertical(100px);
	background: url(../../img/no-img-product.png) no-repeat center;
	background-size: contain; }

/* 404 page end */

/* logo */
.logo {
	// has styles on layout.sass
	position: relative;
	float: left;
	background-color: $base-color;
	z-index: 5;

	@include media('<desk-sm') {
		float: none;
		display: block;
		background-color: transparent; }

	&::before {
		@include pseudo();
		@include position(0);
		@include size(100%);
		background-repeat: no-repeat;
		background-image: url(../../img/logo.png);
		background-size: auto 100%;
		background-position: left;

		.home-page &, .page--has-full-logo & {
			background-size: contain;
			background-position: center; }

		@include media('<desk-sm') {
			background-image: url(../../img/logo--alt.png);
			background-size: contain;
			background-position: center; } }

	a, & > span {
		position: relative;
		display: block;
		height: 100%;
		z-index: 5;

		& > em {
			@include hide-text; } } }

/* logo end */

/* scroll to */
.scroll-to {
	text-align: center;
	color: $text-color;
	transition: {
		property: color;
		duration: $animation-speed; }

	.scroll-is-bottom & {
		color: #ccc; }

	a {
		@include font-d;
		text-decoration: none;
		color: inherit; }

	svg {
		@include display-inline-block-middle;
		@include size(40px);

		@include media('<desk-lg') {
			@include size(30px); } }

	path {
		fill: currentColor; }

	// hover
	a:hover {
		color: $link-color; } }

/* scroll to end */

/* short contacts */
.short-contacts-wrap {
	text-align: center;
	margin-bottom: 2rem;

	&:last-child {
		margin-bottom: 0; } }

.short-contacts {
	$icon-size: 22px;
	$icon-space: 14px;

	&, .#{$user-content} & {
		list-style: none;
		margin: 0;
		padding: 0;

		li {
			padding: 0 0 10px;
			margin: 0;

			&:last-child {
				padding-bottom: 0; }

			&::before {
				content: none; } } }

	& {
		@include display-inline-block-top;
		text-align: left;
		width: 100%; }

	li {
		@include clear-spacing;
		@include clear;
		color: $text-color;
		cursor: default; }

	svg {
		float: left;
		@include size($icon-size);
		margin-right: $icon-space; }

	path, use {
		fill: $text-color-light; }

	a, & li > div {
		display: block;
		@include font(16px, #{$icon-size});
		margin-left: #{$icon-size + $icon-space};
		color: inherit;
		text-decoration: none;
		//transition:
		//roperty: color
		//uration: $animation-speed

		sub, sup {
			font-size: 0.6em; }

		& ~ a {
			margin-top: 5px; }

		span {
			letter-spacing: normal; }

		&[href] {
			span {
				border-bottom: 1px solid tint($text-color, 40%);

				@if $animation-use {
					transition: {
						property: border-color;
						duration: $animation-speed; } } } }

		&[href^="mailto:"] {
			word-wrap: break-word; }

		&[href^="tel:"], a[href] {
			span {
				border-bottom-color: transparent; } }

		.no-touchevents &[href]:not([href^="tel:"]):hover {
			color: $base-color;

			span {
				border-bottom: 1px solid tint($base-color, 40%); } } } }

/* short contacts end */

/* departments */
$dep-space: 24px, 28px;
$dep-img-size: 80px, 120px; // width, height

.dep-list {
	border-style: solid;
	border-width: 0 1px 1px;
	border-color: $border-color;
	//display: flex
	//flex-direction: column-reverse
	@include clear-spacing;

	&, .#{$user-content} & {
		list-style: none;
		margin: 0 0 $bottom-space--md;
		padding: 0;

		@include media('<desk-lg') {
			margin-bottom: $bottom-space--ml; }

		&:last-child {
			margin-bottom: 0; }

		& > li {
			//padding: nth($dep-space, 1) #{nth($dep-space, 2) + nth($dep-img-size, 1)} nth($dep-space, 1) nth($dep-space, 2)
			padding: nth($dep-space, 1) nth($dep-space, 2);
			margin: 0;

			@include media('<mob-lg') {
				padding-left: $layout-spaces--mob; }

			&::before {
				content: none; } } }

	& > li {
		position: relative;
		border-style: solid;
		border-color: inherit;
		border-width: 1px 0 0;
		min-height: 105px;
		background-color: $bg-color;
		z-index: 5;
		overflow: hidden;

		&:first-child {
			overflow: visible; } } }

.dep-img {
	position: absolute;
	right: nth($dep-space, 2);
	bottom: 0;
	padding: 0;
	margin: 0;
	width: nth($dep-img-size, 1);
	@include font($font-size, $letter-spacing);

	&::before {
		@include pseudo-block-relative();
		@include ratio(nth($dep-img-size, 1), nth($dep-img-size, 2)); }

	& > div {
		position: absolute;
		@include size(100%);
		@include position(0); }

	img {
		@include object-fit(contain);
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		opacity: 0.5;
		filter: grayscale(1);

		.touchevents & {
			opacity: 1;
			filter: grayscale(0); } }

	& > div:first-child img {
		filter: brightness(0) invert(1);
		opacity: 1; } }

// hover
.dep-list > li:hover {
	z-index: 10 !important;
	overflow: visible !important;

	&::after {
		content: none; }

	.dep-img {
		&::after {
			opacity: 0; }

		img {
			filter: grayscale(0); } } }

.dep-sub-list {

	&, .#{$user-content} & {
		list-style: none;
		margin: 0;
		padding: 0 10px 0 0;

		li {
			padding: 0;
			margin: 0 0 18px;

			&::before {
				content: none; }

			&:last-child {
				margin-bottom: 0; } } }

	li {
		@include font-d;
		color: inherit;

		a {
			color: inherit;
			text-decoration: none;

			&:hover {
				color: $base-color; } } } }

// horizontal view
.dep-list--horizontal {
	$items-in-row: 4;
	$items-in-row--hd: 2;

	display: block;
	border-width: 0;

	&, .#{$user-content} & {
		& > li {
			margin: -1px 0 0 -1px; } }

	& > li {
		@include display-inline-block-top;
		width: calc(#{round-float(100%/$items-in-row, 2)} + 1px);
		border-width: 1px;
		overflow: hidden;

		&:nth-child(-n+#{$items-in-row}) {
			overflow: visible; }

		@include media('<desk-hd') {
			width: calc(#{round-float(100%/$items-in-row--hd, 2)} + 1px);
			overflow: hidden;

			&:nth-child(-n+#{$items-in-row}) {
				overflow: hidden; }

			&:nth-child(-n+#{$items-in-row--hd}) {
				overflow: visible; } }

		@include media('<desk-md') {
			width: calc(#{round-float(100%/$items-in-row, 2)} + 1px);

			&:nth-child(-n+#{$items-in-row}) {
				overflow: visible; } }

		@include media('<desk-sm') {
			width: calc(#{round-float(100%/$items-in-row--hd, 2)} + 1px);
			overflow: hidden;

			&:nth-child(-n+#{$items-in-row}) {
				overflow: hidden; }

			&:nth-child(-n+#{$items-in-row--hd}) {
				overflow: visible; } } } }

/* departments end */

/* main slider (home page) */
.main-slider {
	//width: 600px
	height: 100%;
	background-color: $bg-color-alt;
	color: $main-slider-color;

	@include media('<mob-lg') {
		color: $text-color;
		padding-bottom: 60px; } }

.main-slider-prev, .main-slider-next, .main-slider-pagination, .main-slider__content {
	//filter: drop-shadow(1px 1px rgba(white, 0.5))
	user-select: none; }

.main-slider-prev, .main-slider-next {
	&::before, &::after {
		border-color: $main-slider-color;

		@include media('<mob-lg') {
			border-color: $text-color; } } }

.main-slider-pagination {
	position: absolute;
	top: 0;
	left: 0;
	bottom: auto;
	width: auto;
	min-width: 35px;
	padding-right: 20px;
	@include font-alt-italic;
	@include font(20px, 1);

	@include media('<mob-lg') {
		position: absolute;
		width: auto;
		min-width: 85px;
		@include padding-horizontal(25px);
		transform: translate(-50%, 0);
		@include font-alt-italic;
		@include font(18px, 1);
		text-align: center;
		color: $text-color; }

	&::after {
		@include pseudo();
		left: 100%;
		top: 50%;
		@include size(100px, 0);
		border-top: 1px solid $main-slider-color;
		margin-top: -1px;

		@include media('<desk-sm') {
			width: 50px; }

		@include media('<mob-lg') {
			content: none; } } }

.main-slider__item {
	position: relative;
	text-align: center;

	&::before {
		$grad-color: black;
		@include pseudo();
		@include size(100%);
		@include position(0);
		//+linear-gradient(to right, rgba($grad-color, 0.33) 0%, rgba($grad-color, 0) 100%)
		background-color: rgba($grad-color, 0.25);
		z-index: -1;

		@include media('<mob-lg') {
			content: none; } }

	&::after {
		content: "";
		display: inline-block;
		vertical-align: middle;
		height: 100%; } }

.main-slider__img {
	position: absolute;
	@include size(100%);
	@include position(0);
	background-color: $bg-color-alt;
	z-index: -2;

	@include media('<mob-lg') {
		position: relative;
		@include size(auto);
		@include position(auto);
		z-index: auto;
		margin: -#{$layout-spaces--mob} -#{$layout-spaces--mob} 2.5rem; }

	&::before {
		content: none;
		display: block;
		@include ratio(172, 85);

		@include media('<mob-lg') {
			content: ""; } }

	img {
		display: block;
		@include size(100%);
		max-width: none;
		@include object-fit(cover);

		@include media('<mob-lg') {
			position: absolute;
			left: 0;
			top: 0; } } }

.main-slider__content {
	@include display-inline-block-top;
	width: 1200px;
	max-width: calc(100% - 10px);
	text-align: left;

	@include media('<desk-hd') {
		width: 980px; }

	@include media('<desk-sm') {
		vertical-align: middle;
		width: 750px; } }

.main-slider__title {
	$font-size: 72px;
	$font-size--hd: 60px;
	$font-size--lg: 48px;
	$font-size--md: 44px;
	$font-size--sm: 30px;
	$font-size--mob: 26px;
	$line-height: 1;
	$line-height--sm: round-float(36/30, 1);
	// ===============
	display: block;
	overflow: hidden;
	margin-bottom: $bottom-space--lg;
	@include font-alt-bold;
	@include font($font-size, $line-height);
	width: 700px;
	max-width: 100%;
	max-height: round($font-size*$line-height*5); // maximum 5 line
	cursor: default;

	@include media('<desk-sm') {
		width: 600px;
		font-size: $font-size--md;
		max-height: round($font-size--md*$line-height*5); } // maximum 5 line

	@include media('<mob-lg') {
		width: auto;
		font-size: $font-size--sm;
		line-height: $line-height--sm;
		max-height: round($font-size--sm*$line-height--sm*5); // maximum 5 line
		margin-bottom: 2rem; }

	@include media('<mob-md') {
		font-size: $font-size--mob;
		max-height: round($font-size--mob*$line-height--sm*5); } // maximum 5 line

	&:last-child {
		margin-bottom: 0; } }

.main-slider__footer {
	.more-link {
		@include display-inline-block-top;
		@include font-d;
		text-decoration: none;
		color: inherit;

		span {
			@include display-inline-block-top;
			line-height: 1.5;
			border-bottom: 2px solid; }

		&:hover {
			color: $base-color;

			span {
				border-color: $base-color; } } } }

/* main slider (home page) end */

/* catalog - m-catalog (home page) */
.m-catalog-title {
	display: block;
	@include font-alt-bold;
	@include font($m-catalog-title__font-size, 1);
	letter-spacing: 0.05em;
	margin-bottom: 0.7em;

	@include media('<desk-hd') {
		font-size: $m-catalog-title__font-size--hd; }

	@include media('<desk-lg') {
		font-size: $m-catalog-title__font-size--lg; }

	@include media('<mob-lg') {
		margin-bottom: 0.5em; }

	@include media('<mob-md') {
		display: none; }

	&:last-child {
		margin-bottom: 0; } }

.m-catalog-list {
	border-style: solid;
	border-width: 0;
	border-color: $border-color;
	@include clear-spacing;

	// при переполненние контейнера (если добавить больше 8 категорий)
	// кастомный скролл (включив опцию fullPage.js scrollOverflow: true) не получается добавить
	// если высота контента (списка) нечетная, то округление высоты одного элемента списка (.m-catalog-item) происходит в болюшу сторону. Добавляется 1 лишний пиксел к общей высоте. Появляется скролл, хотя элементов не больше 8
	// Если же высоту элементов делать на 1 пиксел, то получается однопикселный отступ снизу, что будет резать глаз дизайнеру, менеджерам, клиенту и самому Б.
	// Опцию scrollOverflow не включаем. Лишние (больше 8) элементы списка обрезаются!

	&, .#{$user-content} & {
		list-style: none;
		margin: 0 -1px 0 0;
		padding: 0;

		li {
			margin: -1px 0 0 -1px;

			&::before {
				content: none; } } }

	& > li {
		// has styles on layout.sass
		//+display-inline-block-top
		float: left;
		overflow: hidden;
		width: calc(25% + 1px);
		position: relative;
		letter-spacing: $letter-spacing;
		border-style: solid;
		border-color: inherit;
		border-width: 1px;
		background-color: $bg-color;
		z-index: 5;

		height: calc((100vh - #{$header-height})/2 + 1px);

		@include media('<desk-lg') {
			height: calc((100vh - #{$header-height--lg})/2 + 1px); }

		@include media('<desk-sm') {
			width: calc(100%/2 + 1px); }

		@include media('<mob-lg') {
			height: auto;
			min-height: 168px;
			width: calc(100% + 1px); }

		@include media('<mob-md') {
			min-height: 138px; }

		&::after {
			@include pseudo();
			@include size(100%, 0);
			left: 0;
			bottom: 100%;
			border-style: solid;
			border-color: inherit;
			border-width: 1px 0 0;
			z-index: 10; }

		&:last-child {
			&::after {
				content: none; } } }

	$data-length: 'data-length' !default;

	@for $i from 1 through 8 {
		@if $i == 1 {
			&[#{$data-length}="#{$i}"] {
				& > li {
					width: calc(100% + 1px);
					height: calc(100vh - #{$header-height} + 1px);
					display: none;

					@include media('<desk-lg') {
						height: calc(100vh - #{$header-height--lg} + 1px); }

					@include media('<desk-sm') {
						height: calc((100vh - #{$header-height--lg})/2 + 1px); }

					&:nth-child(-n+#{$i}) {
						display: block; }

					.m-catalog-img {
						left: auto;
						right: nth($m-catalog__space, 2);
						transform: none;

						@include media('<desk-sm') {
							right: -6%; } } } } }

		@if $i == 2 {
			&[#{$data-length}="#{$i}"] {
				& > li {
					width: calc(100%/2 + 1px);
					height: calc(100vh - #{$header-height} + 1px);
					display: none;

					@include media('<desk-lg') {
						height: calc(100vh - #{$header-height--lg} + 1px); }

					@include media('<desk-sm') {
						height: calc((100vh - #{$header-height--lg})/2 + 1px); }

					&:nth-child(-n+#{$i}) {
						display: block; }

					.m-catalog-img {
						left: auto;
						right: nth($m-catalog__space, 2);
						transform: none;

						@include media('<desk-sm') {
							right: -6%; } } } } }

		@if $i == 3 {
			&[#{$data-length}="#{$i}"] {
				& > li {
					width: calc(100%/2 + 1px);
					display: none;

					&:nth-child(-n+#{$i}) {
						display: block; }

					&:first-child {
						height: calc(100vh - #{$header-height} + 1px);

						@include media('<desk-lg') {
							height: calc(100vh - #{$header-height--lg} + 1px); }

						@include media('<desk-sm') {
							height: calc((100vh - #{$header-height--lg})/2 + 1px);
							width: calc(100% + 1px);

							.m-catalog-img {
								right: nth($m-catalog__space, 2); } } }

					.m-catalog-img {
						left: auto;
						right: nth($m-catalog__space, 2);
						transform: none;

						@include media('<desk-sm') {
							right: -6%; } } } } }

		@if $i == 4 {
			&[#{$data-length}="#{$i}"] {
				& > li {
					width: calc(100%/2 + 1px);
					display: none;

					&:nth-child(-n+#{$i}) {
						display: block; }

					.m-catalog-img {
						left: auto;
						right: nth($m-catalog__space, 2);
						transform: none;

						@include media('<desk-sm') {
							right: -6%; } } } } }

		@if $i == 5 {
			&[#{$data-length}="#{$i}"] {
				& > li {
					width: calc(100%/3 + 1px);
					display: none;

					@include media('<desk-sm') {
						width: calc(100%/2 + 1px); }

					&:nth-child(-n+#{$i}) {
						display: block; }

					&:nth-child(-n+2) {
						width: calc(100%/2 + 1px);

						.m-catalog-img {
							right: nth($m-catalog__space, 2);

							@include media('<desk-sm') {
								right: -6%; } } }

					&:first-child {
						@include media('<desk-sm') {
							height: calc((100vh - #{$header-height--lg})/2 + 1px);
							width: calc(100% + 1px); }

						.m-catalog-img {
							@include media('<desk-sm') {
								right: nth($m-catalog__space, 2); } } }

					.m-catalog-img {
						left: auto;
						right: -6%;
						transform: none; } } } }

		@if $i == 6 {
			&[#{$data-length}="#{$i}"] {
				& > li {
					width: calc(100%/4 + 1px);
					display: none;

					@include media('<desk-sm') {
						width: calc(100%/2 + 1px); }

					&:nth-child(-n+#{$i}) {
						display: block; }

					&:nth-child(7n + 1),
					&:nth-child(6n) {
						width: calc(100%/4*2 + 1px);

						.m-catalog-img {
							left: auto;
							right: nth($m-catalog__space, 2);
							transform: none;

							@include media('<desk-sm') {
								right: -6%; } } }

					.m-catalog-img {
						@include media('<desk-sm') {
							left: auto;
							right: -6%;
							transform: none; } } } } }

		@if $i == 7 {
			&[#{$data-length}="#{$i}"] {
				& > li {
					width: calc(100%/4 + 1px);
					display: none;

					@include media('<desk-sm') {
						width: calc(100%/2 + 1px); }

					&:nth-child(-n+#{$i}) {
						display: block; }

					&:nth-child(7n + 1) {
						width: calc(100%/4*2 + 1px);

						.m-catalog-img {
							left: auto;
							right: nth($m-catalog__space, 2);
							transform: none;

							@include media('<desk-sm') {
								right: -6%; } } }

					&:first-child {
						@include media('<desk-sm') {
							height: calc((100vh - #{$header-height--lg})/2 + 1px);
							width: calc(100% + 1px); }

						.m-catalog-img {
							@include media('<desk-sm') {
								right: nth($m-catalog__space, 2); } } }

					.m-catalog-img {
						left: auto;
						right: -6%;
						transform: none; } } } } } }
.m-catalog-img {
	position: absolute;
	left: 50%;
	bottom: 0;
	padding: 0;
	margin: 0;
	width: nth($m-catalog-img__size, 1);
	max-width: 80%;
	transform: translate(-50%, 0);
	z-index: -1;

	@include media('<desk-hd') {
		width: nth($m-catalog-img__size--hd, 1); }

	@include media('<desk-lg') {
		width: nth($m-catalog-img__size--lg, 1); }

	@include media('<desk-sm') {
		left: auto;
		right: -6%;
		transform: none; }

	@include media('<mob-lg') {
		right: $layout-spaces--mob;
		width: nth($m-catalog-img__size--sm, 1); }

