	position: relative; }

// nav
.contacts-tabs__nav {
	z-index: 5;
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	@include media('<tab-lg') {
		max-width: $text-layout-width;
		@include push-auto; }

	@include media('<960px') {
		max-width: none; } }

.contacts-tabs__thumbs {
	@include display-inline-block-top;
	@include clear-spacing;
	position: relative; }

.contacts-tabs__tab {
	$this-space: 1.6em;
	$this-font-size: 36px;
	$this-font-size--md: 32px;
	$this-font-size--sm: 26px;
	//=================
	position: relative;
	@include display-inline-block-top;
	@include font-alt-bold;
	@include font($this-font-size, round-float(42/36, 2));
	letter-spacing: $letter-spacing;
	padding-right: $this-space;

	@include media('<desk-lg') {
		font-size: $this-font-size--md; }

	@include media('<desk-sm') {
		font-size: $this-font-size--sm; }

	@include media('<mob-lg') {
		display: block;
		padding-right: 0;
		margin-bottom: 0.4em; }

	&::after {
		@include pseudo();
		@include size(0, $this-font-size);
		top: 50%;
		right: round-float($this-space/2, 1); // нужны десятые
		border-left: 2px solid $text-color-light;
		margin-top: -1px;
		transform: translate(0, -50%);

		@include media('<mob-lg') {
			display: none; } }

	&:last-child {
		padding-right: 0;
		margin-bottom: 0;

		&::after {
			content: none; } }

	a {
		text-decoration: none;
		color: inherit;

		span {
			border-bottom: 2px solid transparent;

			@include media('<mob-lg') {
				border-style: dotted;
				border-color: $text-color;
				border-width: 0 0 2px; } }

		// hover
		&:hover:not(.active-tab) {

			span {
				border-color: $text-color; } }

		// active
		&.active-tab {
			color: $base-color;
			cursor: default;

			span {
				@include media('<mob-lg') {
					border-color: transparent; } } } } }

// panels
//.contacts-tabs__panels

//.contacts-tabs__panel

.contacts-box {
	border-top: 1px dashed #b4b4b4;
	padding: $grid-space 0;

	@include media('<tab-lg') {
		max-width: $text-layout-width;
		@include push-auto; }

	@include media('<960px') {
		max-width: none; }

	@include media('<tab-sm') {
		@include padding-vertical(20px); }

	&:last-child {
		padding-bottom: 0; } }

.contacts-box__item {
	margin-bottom: $bottom-space--md;
	@include clear;

	@include media('<tab-sm') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

.contacts-box__label, .contacts-box__title {
	position: relative;
	@include font-d($line-height: 24px); }

.contacts-box__title {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.contacts-box__label {
	float: left;
	padding-right: 120px;
	width: calc(100% - 320px);

	@include media('<desk-lg') {
		padding-right: 80px;
		width: calc(100% - 260px); }

	@include media('<desk-md') {
		padding-right: 10px; }

	@include media('<desk-sm') {
		float: none;
		width: auto;
		padding-right: 0;
		padding-bottom: $bottom-space;
		margin-bottom: $bottom-space; }

	&::after {
		@include pseudo();
		top: 13px;
		right: 50px;
		@include size(50px, 0);
		border: solid $text-color;
		border-width: 1px 0 0;

		@include media('<desk-lg') {
			right: 25px;
			width: 40px; }

		@include media('<desk-md') {
			content: none; }

		@include media('<desk-sm') {
			content: "";
			right: auto;
			left: 0;
			top: auto;
			bottom: 0;
			width: 50px; } }

	.contacts-box__title ~ .contacts-box__item & {
		@include font-normal;
		@include font(16px, 24px);
		text-transform: none;
		letter-spacing: 0.08em; } }

.contacts-box__content {
	overflow: hidden; }

.contacts-box__dt {
	@include font-alt-italic;
	@include font(18px, 24px);
	color: $text-color-light;
	letter-spacing: 0.1em; }

.contacts-box__dd {
	@include font-alt;
	@include font(30px, round-float(36/30, 2));
	margin-bottom: 0.5em;

	@include media('<desk-hd') {
		font-size: 28px; }

	@include media('<desk-lg') {
		font-size: 24px; }

	a {
		color: inherit;
		text-decoration: none;
		word-wrap: break-word;

		&:hover {
			text-decoration: underline; } }

	&:last-child {
		margin-bottom: 0; } }

.contacts__info {
	.user-form {
		padding: 0;
		background: none;

		.layout-text {
			@include media('>=tab-lg') {
				max-width: none; }

			@include media('<960px') {
				max-width: none; } }

		.input-wrap {
			@include media('<desk-sm') {
				margin-bottom: 30px; } }

		.label-holder {
			@include media('<desk-md') {
				width: $uf-label-width--md; }

			@include media('<desk-sm') {
				width: $uf-label-width--sm; }

			@include media(#{$media--md}) {
				width: auto; }

			label {
				@include media('<desk-sm') {
					font-size: 13px; } } }

		.input-holder {
			@include media('<desk-md') {
				margin-left: $uf-label-width--md; }

			@include media('<desk-sm') {
				margin-left: $uf-label-width--sm; }

			@include media(#{$media--md}) {
				margin-left: 0; } }

		.form-footer {
			@include media('<desk-sm') {
				padding-top: 30px; } }

		.form-note {
			@include media('<mob-lg') {
				padding-bottom: 0; } } } }

/* contacts info end */

/* options panel */
.p-options {
	position: relative;
	margin-bottom: 6rem;

	@include media('<desk-sm') {
		margin-bottom: 4rem; }

	@include media('<tab-lg') {
		border: 1px solid $border-color; }

	@include media('<mob-lg') {
		margin-bottom: 3rem; }

	&:last-child {
		margin-bottom: 0; } }

.p-options__list {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	flex-wrap: nowrap;

	//.no-flexbox &
	//isplay: table
	//idth: 100%
	//able-layout: fixed

	@include media('<mob-lg') {
		//display: block
		position: relative;
		padding-right: 60px;
		min-height: 80px; } }

.p-options__item {
	@include font-d($line-height: 1);
	text-align: center;

	@include media('<tab-lg') {
		font-size: 12px; }

	&:first-child {
		text-align: left;
		flex: 1 1;

		@include media('<tab-lg') {
			padding: round($grid-space/2) $grid-space; }

		@include media('<mob-lg') {
			padding: round($grid-space--md/2) $grid-space--md; } }

	&:last-child {
		text-align: right; }

	.no-flexbox & {
		display: table-cell;
		vertical-align: middle; }

	.view-switcher {
		@include display-inline-block-top;

		@include media('<tab-lg') {
			display: block;
			height: 100%; } }

	&.p-options__item--filters-show,
	&.p-options__item--view-switcher {
		@include media('<tab-lg') {
			@include size(60px);
			border: 1px solid $border-color;
			margin: -1px -1px -1px 0; }

		@include media('<mob-lg') {
			position: absolute;
			right: -1px;
			bottom: -1px;
			margin: 0;
			height: calc(50% + 2px); } }

	&.p-options__item--filters-show {
		display: none;

		@include media('<tab-lg') {
			display: block; }

		@include media('<mob-lg') {
			top: -1px;
			height: calc(50% + 1px); } } }

.btn-filters-show {
	position: relative;
	@include display-inline-block-top;
	color: $text-color;
	user-select: none;

	@include media('<tab-lg') {
		display: block;
		height: 100%; }

	& > span {
		@include hide-text; }

	.p-filters-activated-js {
		position: absolute;
		left: 5px;
		top: 5px;
		font-size: 10px;
		line-height: 1;
		letter-spacing: normal;
		color: $base-color;
		z-index: 2;

		&.hide {
			display: none; } }

	svg {
		@include display-inline-block-top;
		@include size(24px);

		.p-options__item & {
			@include media('<tab-lg') {
				position: absolute;
				left: 50%;
				top: 50%;
				margin: -12px 0 0 -12px; } } }

	path {
		fill: currentColor; }

	// hover
	.no-touchevents &:hover {
		color: $base-color; } }


//.view-switcher-wrap
//include media('<tab-lg')
//display: none

.p-options__inner {
	position: relative;
	height: 100%; } // нужно, когда принимают форму квадрата на девайсах

.p-options__group {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	user-select: none;
	text-align: left;

	//+media('<tab-lg')
	//ustify-content: flex-end

	//+media('<tab-sm')
	//ustify-content: center

	.p-options__label {
		@include display-inline-block-middle;
		margin: 4px 25px 4px 0;
		@include font(inherit, inherit);
		color: $text-color-light;

		@include media('<desk-md') {
			margin-right: 15px; }

		@include media('<tab-sm') {
			display: none; } } }

.p-sorting {
	@include display-inline-block-middle;

	&, .#{$user-content} & {
		margin: 0;

		li {
			padding: 0;
			margin: 4px 18px 4px 0;

			@include media('<desk-md') {
				margin-right: 10px; }

			&::before {
				content: none; } } }

	//&.active
	//adding-right: 38px

	li {
		@include display-inline-block-top;
		white-space: nowrap;
		position: relative;
		cursor: pointer;

		// стрелочка развернута ВВЕРХ, сортировака ПО ВОЗРАСТАНИЮ
		.arr {
			position: absolute;
			top: 50%;
			right: 0;
			@include triangle(top, #{$text-color}, 4px, 2);
			margin-top: -4px;
			opacity: 0;
			transform: rotate(0) translateZ(0);
			transition: {
				property: transform, opacity;
				duration: $animation-speed; }

			.order-desc & {
				transform: rotate(0.5turn) translateZ(0); } } }

	a {
		display: block;
		position: relative;
		text-decoration: none;
		color: inherit;
		padding: 0 12px 0 0;

		// hover
		&:hover {
			color: $base-color; }

		& > span em {
			display: none;
			font-style: inherit;
			font-weight: inherit;
			font-family: inherit;

			@include media('<tab-sm') {
				display: inline; } } }

	// активное соотояние
	li.active {

		a {
			color: $base-color;

			& > span {
				border-bottom-color: transparent; }

			// стрелочка развернута ВНИЗ, сортировака ПО УБЫВАНИЮ
			.arr {
				opacity: 1;
				border-bottom-color: $base-color; } } } }

/* options panel end */

/* accordion */
.accordion {
	margin-bottom: $bottom-space--ml;
	border-bottom: 2px solid $border-color;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space; }

	&:last-child {
		margin-bottom: 0; } }

.accordion__item {
	position: relative; }

.accordion__header {
	position: relative;
	z-index: 2;

	.accordion__panel & {
		z-index: 1; } }

.accordion__hand {
	position: relative;
	border: solid $border-color;
	border-width: 0 0 1px;
	padding: nth($accordion-space, 1) nth($accordion-space, 2);
	padding-right: nth($accordion-space, 1)*2+nth($accordion-angle-size, 2);
	cursor: pointer;
	user-select: none;

	&:first-child {
		border-top-width: 1px; }

	@include media('<tab-lg') {
		padding: nth($accordion-space--sm, 1) nth($accordion-space--sm, 2);
		padding-right: nth($accordion-space--sm, 1)*2+nth($accordion-angle-size, 2); }

	@include media('<mob-lg') {
		padding: nth($accordion-space--mob, 1) nth($accordion-space--mob, 2);
		padding-right: nth($accordion-space--mob, 1)*2+nth($accordion-angle-size, 2); }

	.accordion__header__text {
		//+font-alt-bold
		@include font-alt-medium;
		//+font(21px, round-float(27/21, 2))
		@include font(28px, round-float(34/28, 2));

		@include media('<tab-lg') {
			font-size: 24px; }

		@include media('<mob-lg') {
			font-size: 20px; } }

	.accordion__header__angle {
		display: block;
		position: absolute;
		right: nth($accordion-space, 2);
		top: 50%;
		@include size(nth($accordion-angle-size, 1), nth($accordion-angle-size, 2));
		border: 2px solid currentColor;
		border-radius: 50%;
		margin-top: -#{round(nth($accordion-angle-size, 2)*0.5)};
		backface-visibility: hidden;

		@include media('<tab-lg') {
			right: nth($accordion-space--sm, 2); }

		@include media('<mob-lg') {
			right: nth($accordion-space--mob, 2); }

		&::before, &::after {
			@include pseudo();
			left: 50%;
			top: 50%;
			margin: -#{round(nth($accordion-angle-line-size, 2)/2)} 0 0 -#{round(nth($accordion-angle-line-size, 1)/2)};
			backface-visibility: hidden;
			@include size(nth($accordion-angle-line-size, 1), nth($accordion-angle-line-size, 2));
			background-color: currentColor;
			will-change: transform;
			transition: {
				property: transform;
				duration: $animation-speed; } }

		&::before {
			transform: rotate(-180deg) translateZ(0);

			.ie9 & {
				transform: rotate(-180deg); } }

		&::after {
			transform: rotate(-270deg) translateZ(0);

			.ie9 & {
				transform: rotate(-270deg); } } }

	// hover, active
	.no-touchevents &:hover, &.is-open {
		color: $link-color; }

	// active
	&.is-open, &.active-tab {
		.accordion__header__angle {
			&::before, &::after {
				transform: rotate(0) translateZ(0);

				.ie9 & {
					transform: rotate(0); } } } } }

.accordion__panel {
	display: none;
	background-color: $bg-color-alt; }

.accordion__panel__frame {
	position: relative;
	will-change: opacity;
	opacity: 0;
	transition: {
		property: opacity;
		duration: $animation-speed; }

	.is-open > & {
		opacity: 1;
		transition-delay: 0.08s; } }

.accordion__panel__content {
	padding: nth($accordion-space, 1) nth($accordion-space, 2) nth($accordion-space, 1)*2;

	@include media('<tab-lg') {
		padding: nth($accordion-space--sm, 1) nth($accordion-space--sm, 2) nth($accordion-space--sm, 1)*2; }

	@include media('<mob-lg') {
		padding: nth($accordion-space--mob, 1) nth($accordion-space--mob, 2) nth($accordion-space--mob, 1)*2; } }

/* accordion end */
