@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* irs */
.range-slider-js, .p-filters-price-value {
	opacity: 0; }

.irs ~ .p-filters-price-value {
	opacity: 1; }

.irs {
	height: 2px;
	margin-bottom: 20px;

	.p-filters-drop & {
		margin: 6px 0 15px;

		.irs {
			margin: 0; } } }

.irs-line {
	background: #ccc;
	border: none;
	border-radius: 0;
	top: 0;
	height: 100%; }

.irs-bar, .irs-bar-edge {
	top: 0;
	height: 100%;
	background: #333;
	border: none; }

.irs-slider {
	@include size(16px);
	border: 2px solid #333;
	background: #fff;
	box-shadow: none;
	top: 1px;
	transform: translate(0, -50%); }

.irs-min, .irs-max, .irs-from, .irs-to, .irs-single {
	top: auto;
	bottom: 0;
	margin-bottom: 8px;
	opacity: 0;
	will-change: opacity;
	transition: {
		property: opacity;
		duration: $animation-speed; }

	.no-touchevents .irs:hover & {
		opacity: 1; } }

.irs-from, .irs-single, .irs-to {
	padding: 0;
	@include font-normal;
	font-size: 12px;
	color: #333;
	background: none; }

.irs-max, .irs-min {
	padding: 0;
	@include font-normal;
	font-size: 10px;
	color: #ccc;
	background: none; }

/* irs end */
