@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */

.form-caption {
	position: relative;
	display: block;
	margin-bottom: $uf-row-bottom-space;

	.input-wrap + & {
		margin-top: round($uf-row-bottom-space*1.8); }

	@include media(#{$media--md}) {
		margin-bottom: $uf-row-bottom-space--sm;
		position: static;

		.input-wrap + & {
			margin-top: round($uf-row-bottom-space--sm*1.8); } } }

.form-title {
	display: block;
	position: relative;
	@include font-alt-bold;
	@include font(30px, round-float(36/30, 2));
	color: $title-color;
	text-transform: uppercase;
	margin-bottom: $uf-row-bottom-space;

	@include media(#{$media--md}) {
		margin-bottom: $uf-row-bottom-space--sm; }

	@include media(#{$media--sm}) {
		font-size: 24px; }

	.form-caption & {
		width: 50%;

		@include media(#{$media--md}) {
			width: auto; }

		br {
			@include media(#{$media--md}) {
				display: none; } } }

	&::after {
		@include pseudo();
		@include position(100%);
		@include size(100px, 0);
		margin: -0.7em 0 0 20px;
		border-top: 1px solid $text-color;

		@include media('<tab-sm') {
			display: none; } } }

.form-subtitle {
	@include font-alt-bold;
	@include font(36px, round-float(36/36, 2));
	color: $title-color;
	margin-bottom: $uf-row-bottom-space;

	@include media(#{$media--md}) {
		margin-bottom: $uf-row-bottom-space--sm; }

	@include media(#{$media--sm}) {
		font-size: 28px; } }

.btn-reset {
	@include font($font-size, round-float(20/16, 2));
	border: none;
	border-bottom: 1px dashed $text-color;
	background: none;
	box-shadow: none;
	margin: 0;
	padding: 0;

	&:hover {
		color: $base-color;
		border-color: $base-color; }

	.form-caption & {
		position: absolute;
		right: 0;
		bottom: 0;
		margin-bottom: 0.6em;

		@include media(#{$media--md}) {
			margin-bottom: 0; } } }

.user-form {
	& {
		background: $bg-color-alt;
		margin-bottom: $bottom-space--lg; }

	&:last-child {
		margin-bottom: 0; }

	form {
		display: block;
		position: relative; }

	.input-wrap {
		margin-bottom: $uf-row-bottom-space;
		@include clear;

		@include media(#{$media--md}) {
			margin-bottom: $uf-row-bottom-space--sm; } }

	.label-holder {
		float: left;
		width: $uf-label-width;
		margin-bottom: 0;
		padding: 0 20px 0 0;
		@include clear-spacing;
		line-height: 1;

		@include media(#{$media--md}) {
			width: auto;
			float: none;
			padding: 0 0 10px; }

		&::before {
			@include pseudo-inline-block();
			vertical-align: middle;
			min-height: $form-input-height;

			@include media(#{$media--md}) {
				content: none; } }

		label {
			@include font(14px, round-float(17/14, 2));
			letter-spacing: 0.1em;
			@include display-inline-block-middle;
			max-width: calc(100% - 6px);
			padding: 5px 0;
			text-transform: uppercase; } }

	.input-holder {
		margin-left: $uf-label-width;

		@include media(#{$media--md}) {
			margin-left: 0; } }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.form-footer {
		//padding-left: $label-width
		padding: $uf-row-bottom-space 0 0;
		border: solid #d9d9d9;
		border-width: 1px 0 0;
		@include clear;

		@include media(#{$media--md}) {
			padding-top: $uf-row-bottom-space--sm; } }

	.form-buttons {
		float: right;
		margin-left: 30px;

		@include media(#{$media--md}) {
			float: none;
			margin: 0 0 $uf-row-bottom-space--sm;
			text-align: center; }

		.btn-submit {
			width: 200px; } }

	.form-note {
		overflow: hidden;
		min-height: $form-input-height;
		max-width: $uf-label-width;
		@include clear-spacing;
		line-height: 25px;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; }

		@include media(#{$media--md}) {
			max-width: none;
			min-height: 0;
			padding-right: 120px; }

		@include media(#{$media--sm}) {
			padding: 0 0 30px; }

		.form-mark {
			margin-left: 0;
			left: auto; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size, round-float(20/16, 2));
		letter-spacing: normal; } }

/* user form end */
