@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* popups */
// inline popup
.inline-popup {
	max-width: $popup-width;
	background-color: $bg-color-base;
	@include push-auto; }

// default popup
.default-popup {
	max-width: $default-popup-width;
	background-color: $bg-color-alt;
	@include push-auto; }

.inline-popup, .default-popup {
	padding: $popup-space;

	@include media('<desk-lg') {
		padding: $layout-spaces--lg; }

	@include media('<desk-sm') {
		padding: $layout-spaces--sm; }

	@include media('<tab-sm') {
		max-width: none;
		min-height: 100vh;
		padding-top: #{30+$header-height--lg}; }

	@include media('<mob-lg') {
		@include padding-horizontal($layout-spaces--mob);
		padding-bottom: $layout-spaces--mob; }

	.pseudotabs-nav {
		padding-right: 40px;
		margin-bottom: 20px;

		@include media('<tab-sm') {
			padding-right: 0; } }

	.pseudotabs-tab {
		margin-bottom: 20px; } }

// popup title
.inline-popup__title, .default-popup__title {
	@include font-alt-bold;
	@include font(24px, round-float(32/24, 2));
	margin-bottom: $bottom-space--md;
	padding-right: 20px;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

// popup button close
.mfp-close {
	$mfp-close-btn-size: 40px;
	$mfp-close-btn-size--mob: 60px;
	$mfp-close-icon-size: 12px;
	$mfp-close-icon-size--mob: 16px;

	@include size($mfp-close-btn-size);
	opacity: 1;

	@include media('<tab-sm') {
		@include size($mfp-close-btn-size--mob); }

	svg {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size($mfp-close-icon-size);
		margin: -#{round($mfp-close-icon-size/2)} 0 0 -#{round($mfp-close-icon-size/2)};
		z-index: -1;
		pointer-events: none;

		@include media('<tab-sm') {
			@include size($mfp-close-icon-size--mob);
			margin: -#{round($mfp-close-icon-size--mob/2)} 0 0 -#{round($mfp-close-icon-size--mob/2)}; } }

	path {
		fill: currentColor; }

	.mfp-close-btn-in & {
		$bt-space: round($popup-space - (($mfp-close-btn-size - $mfp-close-icon-size)/2));
		$bt-space--lg: round($layout-spaces--lg - (($mfp-close-btn-size - $mfp-close-icon-size)/2));
		$bt-space--sm: round($layout-spaces--sm - (($mfp-close-btn-size - $mfp-close-icon-size)/2));

		fill: $text-color;
		color: $text-color;
		top: $bt-space;
		right: $bt-space;
		margin-top: 4px;

		@include media('<desk-lg') {
			top: $bt-space--lg;
			right: $bt-space--lg; }

		@include media('<desk-sm') {
			top: $bt-space--sm;
			right: $bt-space--sm; }

		@include media('<tab-sm') {
			top: 0;
			right: auto;
			left: 0;
			color: white;
			margin-top: 0;
			background-color: $btn-bg-color; }

		.no-touchevents &:hover {
			color: $link-color; } } }

/* popups end */
