@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* magnific popup */
.mfp-wrap {
	.css-scroll-fixed & {
		overflow-x: hidden;
		overflow-y: auto; } }

.mfp-bg {
	background-color: #3e3e3d; }

.mfp-container {
	max-width: $page-layout-width;

	@include media('<desk-hd') {
		max-width: 100%; }

	@include media('<tab-sm') {
		padding: 0;

		&::before {
			content: none; } } }

//.mfp-content
//include media('<tab-sm')
//height: 100%

/*
 *
 *====== Zoom effect ====== */
.mfp-zoom-in {
	/* start state */
	.mfp-with-anim {
		opacity: 0;
		transition: all 0.2s ease-in-out;
		transform: scale(0.8);

		// для девайсов отключаем эффект всплывание
		@include media('<tab-sm') {
			transform: none; } }

	&.mfp-bg {
		opacity: 0;
		transition: all 0.3s ease-out; }

	/* animate in */
	&.mfp-ready {
		.mfp-with-anim {
			opacity: 1;
			transform: scale(1); }

		&.mfp-bg {
			opacity: 0.5; } }

	/* animate out */
	&.mfp-removing {
		.mfp-with-anim {
			transform: scale(0.8);
			opacity: 0;

			@include media('<tab-sm') {
				transform: none; } }

		&.mfp-bg {
			opacity: 0; } } }

/* magnific popup end */
