@import "../utils/_variables";
@import "../utils/_mixins";
@import "../../libs/include-media/dist/_include-media";

/* products list */
.products {
	overflow: hidden;
	margin-bottom: $bottom-space--lg;

	//+media('<desk-lg')
	//argin-bottom: $bottom-space--ml

	&:last-child {
		margin-bottom: 0; } }

.products__list {
	@include grid-parent($products-space);
	@include clear-spacing;

	@include media('<desk-lg') {
		@include grid-parent($products-space--md); }

	//+media('<tab-lg')
 }	//include grid-parent($products-space--sm)

.products__item {
	@include grid-item(2, $products-space);
	@include font($font-size, $line-height);
	letter-spacing: $letter-spacing;

	@include media('<desk-lg') {
		@include grid-item-responsive(2, $products-space--md, 5rem); }

	.page--has-m-aside & {
		@include media('<desk-md') {
			@include grid-item-responsive(1, $products-space--md, $products-space); } }

	&, .page--has-m-aside & {
		@include media('<tab-lg') {
			@include grid-item-responsive(1, $products-space--md, 5rem); } } }

.products__inner {
	position: relative;
	display: block;
	text-decoration: none;

	a {
		text-decoration: none;
		color: $text-color; }

	@include clear; }

.products__brand {
	position: absolute;
	@include position(0);
	z-index: 10; }

.products__visual {
	float: left;
	width: calc(100% - #{$product-content-width});
	padding-right: $product-visual-space;

	.page--has-m-aside & {
		@include media('<desk-hd') {
			padding-top: 50px;
			width: calc(100% - #{$product-content-width--sm}); }

		@include media('<desk-md') {
			padding-top: 0;
			width: calc(100% - #{$product-content-width}); } }

	&, .page--has-m-aside & {
		@include media('<mob-lg') {
			padding-top: 50px;
			width: calc(100% - #{$product-content-width--sm}); }

		@include media('<mob-md') {
			float: none;
			width: 100%;
			padding-right: 0;
			padding-top: 0;
			margin-bottom: 3rem; } } }

.products__figure {
	display: block;
	position: relative;
	max-width: 240px; // ограничиваем ширину изображения продукта
	@include push-auto;
	text-decoration: none;
	color: inherit;

	@include media('<mob-md') {
		max-width: 160px; }

	&:before {
		@include pseudo-block-relative();
		@include ratio(3, 4); } }

.products__img {
	@include cover-abs();

	.no-image & {
		background: url(../../img/no-img-product.png) no-repeat center;
 }		//background-size: contain

	img {
		display: block;
		width: 100%;
		height: 100%;
		max-width: none;
		@include object-fit(scale-down); } }

.products__title, .products__content, .products__footer {
	float: right;
	clear: right;
	width: $product-content-width;
	margin-bottom: $bottom-space--ml;

	.page--has-m-aside & {
		@include media('<desk-hd') {
			width: $product-content-width--sm; }

		@include media('<desk-md') {
			width: $product-content-width; } }

	&, .page--has-m-aside & {
		@include media('<mob-lg') {
			width: $product-content-width--sm; }

		@include media('<mob-md') {
			float: none;
			clear: none;
			width: 100%;
			margin-bottom: 2rem; } } }

.products__content, .products__footer {
	&:last-child {
		margin-bottom: 0; } }

.products__title {
	@include font-d($line-height: round-float(24/14, 2));
	overflow: hidden;

	a {
		padding-top: 0.5em;
		text-decoration: none;
		color: inherit;
		transition: inherit; }

	.products__model {
		display: none; }

	//hover
	.no-touchevents a:hover & {
		color: $base-color;

		.products__model {
			color: $text-color-light; } } }

.products__model {
	@include font-normal;
	color: $text-color-light;
	letter-spacing: $letter-spacing;

	&::before {
		content: "/ "; } }

// list of characteristics
.products__dl {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.products__di {
	position: relative;
	padding: 5px 0;
	border-bottom: 1px dashed $text-color-light;
	@include clear; }

.products__dt {
	float: left;
	width: $product-dt;
	color: $text-color-light;
	padding-right: 10px; }

.products__dd {
	margin-left: $product-dt; }

// colors
.products__dd {
	.color-pics {
		@include clear-spacing;
		margin-top: -10px;
		padding-top: 8px; } }

// footer
.products__footer {
	@include clear; }

// price
.products__price {
	float: left;
	width: $product-dt;
	padding-right: 10px;

	.cur {
		display: block;
		line-height: 30px;
		color: $base-color;

		.val {
			@include font-bold;
			font-size: 16px;
			letter-spacing: 0.025em; }

		.unit {
			@include font-normal;
			font-size: 12px;
			letter-spacing: 0.05em; } } }

.products__basket {
	margin-left: $product-dt;
	line-height: 1;
	padding-top: 4px; }

// btn to basket
.btn-to-basket, .btn-from-basket {
	$btn-space: 10px;
	//====================
	@include display-inline-block-top;
	//position: absolute
	//right: 0
	//top: 0
	cursor: default;
	color: inherit;
	text-align: center;
	white-space: nowrap;
	@include clear-spacing;
	user-select: none;

	//svg, span
	//include display-inline-block-middle
	svg {
		display: block;
		float: left; }

	.svg-ico-basket {
		@include size(22px); }

	.svg-ico-basket-add {
		@include size(32px, 22px); }

	path {
		fill: currentColor; }

	span {
		display: block;
		overflow: hidden;
		padding-right: $btn-space;
		@include font(1.6rem, round-float(20/16, 2));
		letter-spacing: $letter-spacing;
		text-align: left;
		white-space: normal; }

	svg + span {
		padding: 2px 0 0 $btn-space; } }

// hover
a.btn-to-basket {
	cursor: pointer;

	.no-touchevents &:hover {
		color: $link-color; } }

.btn-from-basket {
	&, .grid-view & {
		display: none; }

	.touchevents & {
		color: $base-color; }

	&:hover {
		color: $base-color; } }

/* grid view */
.grid-view {
	.products__list {
		@include grid-parent($products-grid-space);
		@include clear-spacing;

		@include media('<mob-lg') {
			@include grid-parent($products-grid-space--md); } }

	.products__item {
		@include grid-item(4, $products-grid-space, $products-space);

		.page--has-m-aside & {
			@include media('<desk-md') {
				@include grid-item-responsive(3, $products-grid-space, $products-space); }

			@include media('<desk-sm') {
				@include grid-item-responsive(3, $products-grid-space, $products-space--md); } }

		&, .page--has-m-aside & {
			@include media('<mob-lg') {
				@include grid-item-responsive(2, $products-grid-space--md, $products-space--md); }

			@include media('<mob-md') {
				@include grid-item-responsive(1, $products-grid-space--md, $products-space--md); } } }

	.products__visual, .products__title, .products__content, .products__footer {
		float: none;
		width: auto; }

	.products__visual {
		padding: 0;
		margin-bottom: $bottom-space--ml;

		&:last-child {
			margin-bottom: 0; } }

	.products__title {
		//+padding-horizontal($grid-space)
		@include font-normal;
		@include font(16px, round-float(24/16, 2));
		letter-spacing: $letter-spacing;
		text-align: center;
		text-transform: none;
		max-width: 260px;
		margin: 0 auto 1.5rem;

		.products__model {
			display: inline; } }

	.products__content {
		display: none; }

	// price
	.products__price {
		text-align: center;
		float: none;
		width: auto;
		padding: 0; }

	.products__basket {
		position: absolute;
		top: 0;
		right: 0;
		margin: 0;
		z-index: 10; }

	.btn-to-basket,
	.btn-from-basket {
		span {
			display: none; } }

	.btn-to-basket {
		// hide
		display: none;

		.touchevents & {
			display: inline-block; } }

	.products__inner:hover .btn-to-basket {
		display: inline-block; } }

.in-cart {
	.grid-view &, & {
		.btn-to-basket,
		.products__inner:hover .btn-to-basket {
			display: none; }

		.btn-from-basket {
			display: inline-block; } } }

/* grid view end */

// для маленьких мониторов
/* for mobile */
@include media('<tab-lg');

.products__item {
	@include media('<mob-lg'); }

/* for mobile end */
/* products list end */

